<template>
	<view class="wrapper">
		<cu-custom bgColor="text-white bg-header" :isBack="true">
			<block slot="backText">填写领取信息</block>
		</cu-custom>

		<!-- 头部 -->
		<view class="navbar">
			<view v-for="(item, index) in navList" :key="index" class="nav-item"
				:class="{ current: tabCurrentIndex === index }" @click="tabClick(index)">
				{{ item.text }}
			</view>
		</view>
		<!-- 显示区域 -->
		<view class="list" v-for="(item, index) in navList" :key="index" v-if="tabCurrentIndex === index">
			
			<!-- 快递配送 -->
			<block v-if="index == 0">
				<view class="address-form-wrap">
					<form @submit="address-form" class="address-form">
						<view class="address-form-item">
							<view class="label">收货地址：</view>
							<view class="form-item-wrap">
								<image class="icon-location" src="../../static/images/icon-location.png"></image>
								<input type="text" placeholder-style="color:#888888" class="address-input"
									placeholder="点击选择收货地址">
								<image class="icon-arrows" src="../../static/images/icon-arrows-grey.png"></image>
							</view>
						</view>
						<view class="address-form-item">
							<text class="label">门牌号：</text>
							<view class="form-item-wrap"><input type="text" placeholder-style="color:#888888"
									class="address-input" placeholder="详细地址，例1层101室"></view>
						</view>
						<view class="address-form-item">
							<text class="label">联系人：</text>
							<view class="form-item-wrap">
								<input type="text" placeholder-style="color:#888888" class="address-input"
									placeholder="请填写收货人姓名">
							</view>
						</view>
						<view class="address-form-item">
							<text class="label">手机号：</text>
							<view class="form-item-wrap"><input type="text" placeholder-style="color:#888888"
									class="address-input" placeholder="请填写收货人手机号码"></view>
						</view>
						<view class="address-form-item">
							<button class="btn-address-submit">保存地址</button>
						</view>
					</form>
				</view>
			</block>

			<!-- 到店使用 -->
			<block v-if="index == 1">
				<view class="qrcode-wrap">
					<view class="qrcode-cover">
						<image class="qrcode" src="https://z3.ax1x.com/2021/07/06/RoPJSJ.png"></image>
					</view>
					<view class="text">请向店员出示二维码</view>
				</view>
			</block>
		</view>



	</view>
</template>

<script>
	export default {
		data() {
			return {
				tabCurrentIndex: 0,
				navList: [{
						state: 0,
						text: '快递配送',
						orderList: []
					},
					{
						state: 1,
						text: '到店使用',
						orderList: []
					}
				]
			}
		},
		onLoad(options) {
			// 页面显示是默认选中第一个
			this.tabCurrentIndex = 0;
		},

		methods: {
			changeTab(e) {
				this.tabCurrentIndex = e.target.current;
			},
			//顶部tab点击
			tabClick(index) {
				this.tabCurrentIndex = index;
			}
		}
	}
</script>

<style lang="scss">
	page {
		background: #ffffff;
	}

	.swiper-box {
		height: calc(100% - 40px);
	}

	.list-scroll-content {
		height: 100%;
	}

	.navbar {
		display: flex;
		height: 40px;
		background: #FFF0ED;
		;
		position: relative;
		z-index: 10;

		.nav-item {
			flex: 1;
			display: flex;
			justify-content: center;
			align-items: center;
			height: 100%;
			font-size: 15px;
			color: #666666;
			position: relative;

			&.current {
				color: #000000;
				background: #ffffff;
			}
		}
	}


	.address-form-wrap {
		.address-form-item {
			display: flex;
			line-height: 30rpx;
			font-size: 28rpx;
			padding: 40rpx 20rpx;
			margin: 0 20rpx;
			border-bottom: 1rpx solid #F1F1F1;
			align-items: center;

			&:nth-last-child(1) {
				border: none;
			}

			.label {
				display: inline-block;
				width: 190rpx;
				// flex: 1;
				font-size: 28rpx;
				font-weight: 400;
				color: #000000;
			}

			.form-item-wrap {
				position: relative;

				.address-input {
					font-size: 28rpx;
					font-weight: 400;
					color: #888888;
					display: inline-block;
					vertical-align: middle;
				}

				.icon-location {
					display: inline-block;
					width: 17rpx;
					height: 21rpx;
					vertical-align: top;
					margin-top: 6rpx;
				}

				.icon-arrows {
					position: absolute;
					right: 0;
					top: 15rpx;
					display: inline-block;
					width: 13rpx;
					height: 25rpx;
				}
			}

			.btn-address-submit {
				font-size: 30rpx;
				font-family: Source Han Sans CN;
				font-weight: 400;
				color: #FFFFFF;
				width: 350rpx;
				height: 68rpx;
				line-height: 68rpx;
				background: #FD5C40;
				border-radius: 34rpx;
			}
		}
	}

	.qrcode-wrap {
		text-align: center;
		.qrcode-cover {
			width: 363rpx;
			height: 363rpx;
			margin: 110rpx auto 0 auto;
		}
		.qrcode {
			display: inline-block;
			width: 100%;
			height: 100%;
		}
		.text {
			display: inline-block;
			margin-top: 60rpx;
			font-size: 30rpx;
			font-weight: 400;
			color: #000000;
		}
	}
</style>
